import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
    {
      path: '/',
      name: 'home',
      meta:{
        layout: 'main'
      },
      component: () => import('../views/Home.vue') 
    },
    // {
    //   path: '/cms/login',
    //   name: 'CMS',
    //   component: () => import('../views/cms/Login.vue') 
    // },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
