window.addEventListener('load', function(){

    function modalTransitionHandler(){
        this.parentNode.classList.remove('open');
        document.body.classList.remove('no-scroll');
        this.removeEventListener('transitionend', modalTransitionHandler);
    }
    
    let modalTargets = document.querySelectorAll('[data-modal-target]');
    let modals = document.querySelectorAll('[data-modal-window]');    
    
    for (let i = 0; i <= modalTargets.length - 1; i++){

        modalTargets[i].addEventListener('click', function (){

            let attribute = this.getAttribute('data-modal-target');            
            let childModal = document.querySelector(`[data-modal-window="${attribute}"]`);

            document.body.classList.add('no-scroll');
            childModal.classList.add('open');

            setTimeout(function(){

                let childLayout = childModal.getElementsByClassName('modal-window__layout');
                let childModalContainer = childModal.getElementsByClassName('modal-window__container');

                childLayout[0].classList.add('open');
                childModalContainer[0].classList.add('open');

            },10);
            
        });
        
    }

    for (let i = 0; i <= modals.length - 1; i++){        
        
        let layout = modals[i].getElementsByClassName('modal-window__layout');        
        let container = modals[i].getElementsByClassName('modal-window__container');
        let closeButton = modals[i].getElementsByClassName('modal-window__closer');
        
        layout[0].addEventListener('click', function(){
            
            layout[0].addEventListener('transitionend', modalTransitionHandler);            
            container[0].classList.remove('open');
            this.classList.remove('open');

        });

        closeButton[0].addEventListener('click', function(){            
                    
            layout[0].addEventListener('transitionend', modalTransitionHandler);
            container[0].classList.remove('open');
            layout[0].classList.remove('open');          

        });
    }
})